import React, { Fragment, useState } from "react";
import "./App.css";

import { parse, differenceInSeconds, format } from "date-fns";

const MINUTE = 60;
const HOUR = 60;
const TIME_FORMAT = "HH:mm:ss";

function App() {
  const [timeStart, setTimeStart] = useState("14:30:00");
  const [timeEnd, setTimeEnd] = useState("14:45:00");
  // 今回、求めたい時速の回転数
  const [rotateNumberPerHour, setRotateNumberPerHour] = useState(0);
  const [elapsedSeconds, setElapsedSeconds] = useState(0);
  const [rotateNumberStart, setRotateNumberStart] = useState("100");
  const [rotateNumberEnd, setRotateNumberEnd] = useState("200");

  const changeTimeStart = (event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
    setTimeStart(event.target.value);
  };
  const changeTimeEnd = (event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
    setTimeEnd(event.target.value);
  };
  const changeRotateNumberStart = (event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
    setRotateNumberStart(event.target.value);
  };
  const changeRotateNumberEnd = (event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
    setRotateNumberEnd(event.target.value);
  };

  // 計算ボタン押下
  const calcRotateNumberPerHour = () => {
    // 時間の文字列を日付オブジェクトに変換する。
    const dateStart = parse(timeStart, TIME_FORMAT, new Date());
    const dateEnd = parse(timeEnd, TIME_FORMAT, new Date());

    // 差の秒を求める
    const elapsedSecondsResult: number = differenceInSeconds(dateEnd, dateStart);
    setElapsedSeconds(elapsedSecondsResult);

    // 1秒辺りの回転数を計算する。
    const rotateNumber = Number(rotateNumberEnd) - Number(rotateNumberStart);
    const rotateNumberPerSecondResult = rotateNumber / elapsedSecondsResult;
    // 時速の回転数を求める。
    const rotateNumberPerHourResult = Math.floor(rotateNumberPerSecondResult * MINUTE * HOUR);
    setRotateNumberPerHour(rotateNumberPerHourResult);
  };

  // 時刻を自動入力する。
  const inputStartTime = () => {
    // 現在時刻を時間分秒の形にして、テキストボックスに書き込む
    const date = format(new Date(), TIME_FORMAT);
    setTimeStart(date);
  };
  const inputEnd = () => {
    const date = format(new Date(), TIME_FORMAT);
    setTimeEnd(date);
  };

  return (
    <div className="App">
      <label htmlFor="">開始回転数</label>
      <input
        className="input"
        type="number"
        value={rotateNumberStart}
        onChange={(event) => {
          changeRotateNumberStart(event);
        }}
      />

      <label htmlFor="">開始時刻</label>
      <input
        className="input"
        type="text"
        value={timeStart}
        onChange={(event) => {
          changeTimeStart(event);
        }}
      />
      <button className="calcBtn" onClick={inputStartTime}>
        開始時刻入力
      </button>
      <label htmlFor="">終了回転数</label>
      <input
        className="input"
        type="number"
        value={rotateNumberEnd}
        onChange={(event) => {
          changeRotateNumberEnd(event);
        }}
      />

      <label htmlFor="">終了時刻</label>
      <input
        className="input"
        type="text"
        value={timeEnd}
        onChange={(event) => {
          changeTimeEnd(event);
        }}
      />
      <button className="calcBtn" onClick={inputEnd}>
        終了時刻入力
      </button>
      <button className="calcBtn" onClick={calcRotateNumberPerHour}>
        計算
      </button>

      <div className="display">
        <Fragment>
          <span>経過時間</span>
          <span className="number">{(elapsedSeconds / MINUTE).toFixed(1)}</span>
          <span>分</span>
        </Fragment>
        <Fragment>
          <span>回転数</span>
          <span className="number">{Number(rotateNumberEnd) - Number(rotateNumberStart)}</span>
          <span>回転</span>
        </Fragment>
        <Fragment>
          <span>時速</span>
          <span className="number">{rotateNumberPerHour}</span>
          <span>回転</span>
        </Fragment>
      </div>
    </div>
  );
}

export default App;
